      <!-- Ìò∏ÌÖî Í¥ÄÎ¶¨ -->
      <div v-if="currentView === 'HotelAdminAccounts' && isVerified">
          <!-- ÎπÑÎ∞ÄÎ≤àÌò∏ Ïù∏Ï¶ùÏù¥ ÏôÑÎ£åÎêú Í≤ΩÏö∞ -->
          <div class="top-container">
            <div class="title">
              <h2>Ìò∏ÌÖî Í¥ÄÎ¶¨Ïûê Í≥ÑÏ†ï Í¥ÄÎ¶¨</h2>
            </div>
            <div class="search-container">
              <input
                class="search-input"
                type="text"
                placeholder="Ïù¥Î¶Ñ, Ïù¥Î©îÏùºÎ°ú Í≤ÄÏÉâÌï¥Ï£ºÏÑ∏Ïöî."
                v-model="searchKeyword"
                @input="handleHotelSearch"
              />
              <button class="styled-button" @click="openModal">Ìò∏ÌÖî Í¥ÄÎ¶¨Ïûê Í≥ÑÏ†ï ÏÉùÏÑ±</button>
