              <span>Ìò∏ÌÖîÎ™Ö</span>
              <span>Ïù¥Î©îÏùº</span>
              <span>Ï†ÑÌôîÎ≤àÌò∏</span>
              <span>Í¥ÄÎ¶¨</span>
            </div>
            <!-- ÌÖåÏù¥Î∏î ÎÇ¥Ïö© -->
            <div
              v-for="user in hotelManagerList"
              :key="user.id"
              class="user-table-row"
            >
              <span :class="user.isActive ? 'user-active' : 'user-deactive'">{{
                user.isActive
              }}</span>
              <span>{{ user.userId }}</span>
              <span>{{ user.name }}</span>
              <span>{{ user.email }}</span>
